import React, { useEffect, useState } from 'react'

import { Link } from "gatsby"

import scroll_left from "../images/scroll_left.svg"
import scroll_right from "../images/scroll_right.svg"
import bannerpic1 from "../images/bannerpic1.jpg"
import bannerpic2 from "../images/bannerpic2.jpg"

import "react-responsive-carousel/lib/styles/carousel.min.css"; // requires a loader
import { Carousel } from 'react-responsive-carousel';

export default function Slider() {
    const lastSlide = 1;

    const [currentSlide, setCurrentSlide] = useState(0);

    const previous = () => {
        if (currentSlide === 0) setCurrentSlide(lastSlide);
        else setCurrentSlide(currentSlide - 1);
    }
    const next = () => {
        if (currentSlide === lastSlide) setCurrentSlide(0);
        else setCurrentSlide(currentSlide + 1);
    }

    const updateCurrentSlide = (idx) => {
        if (idx === currentSlide) return;

        if (idx > lastSlide) setCurrentSlide(0);
        else setCurrentSlide(1);
    }

    useEffect(() => {
        console.log('currentSlide', currentSlide)
    }, [currentSlide])

    return (
        <>
            {/* BUTTONS */}
            <div id="prevnext" style={{ zIndex: 10 }}>
                <span>
                    <a href="#" className="left" id="previous_slide" onClick={previous}>
                        <img src={scroll_left} alt="anterior" />
                    </a>
                </span>
                <span>
                    <a href="#" className="right" id="next_slide" onClick={next}>
                        <img src={scroll_right} alt="próxima" />
                    </a>
                </span>
            </div>

            <Carousel
                autoPlay={true}
                infiniteLoop={true}
                selectedItem={currentSlide}
                onChange={updateCurrentSlide}
                showArrows={false}
                showIndicators={false}
                showStatus={false}
                interval={5000}
                stopOnHover={true}
            >
                <div className="row">
                    {/* Chamadas destaque */}
                    <div className="col-1 col-12-medium"></div>
                    <div className="col-5 col-12-medium">
                        <h1>Origens Brasil<span className="registered">®</span></h1>
                        <div className="barrinha"></div>
                        <p>
                            O Engajados desempenha a coordenação técnica de todas as plataformas do premiado projeto Origens Brasil ® <br /><br />
                            <Link className="button primary icon fa-eye to-front desktop-only" to="/projetos/origens">
                                SAIBA MAIS
                            </Link>
                        </p>
                    </div>

                    {/* grid fotos */}
                    <div className="col-5 col-12-medium">
                        <div className="box alt">
                            <div className="row gtr-50 gtr-uniform">
                                <div className="col-8">
                                    <span className="image fit"><img
                                        src={bannerpic1}
                                        data-clip="hexagon"
                                        alt="destaque grande banner"
                                    /></span>
                                </div>
                                <div className="col-4">
                                    <span className="image fit"><img
                                        src={bannerpic2}
                                        data-clip="hexagon"
                                        alt="destaque peq banner"
                                    /></span>
                                </div>
                            </div>
                        </div>
                        <p>
                            <Link className="button primary icon fa-eye to-front mobile-only" to="/projetos/origens">
                                SAIBA MAIS
                            </Link>
                        </p>
                    </div>
                    <div className="col-1 col-12-medium"></div>
                </div>

                <div className="row">
                    {/* Chamadas destaque */}
                    <div className="col-1 col-12-medium"></div>
                    <div className="col-5 col-12-medium">
                        <h1>Outro projeto</h1>
                        <div className="barrinha"></div>
                        <p>
                            Linha de destaque para o projeto tal que é muito interessante, de verdade, pode acreditar! <br /><br />
                            <Link className="button primary icon fa-eye to-front desktop-only" to="/test">
                                SAIBA MAIS
                            </Link>
                        </p>
                    </div>

                    {/* grid fotos */}
                    <div className="col-5 col-12-medium">
                        <div className="box alt">
                            <div className="row gtr-50 gtr-uniform">
                                <div className="col-8">
                                    <span className="image fit"><img
                                        src={bannerpic1}
                                        data-clip="hexagon"
                                        alt="destaque grande banner"
                                    /></span>
                                </div>
                                <div className="col-4">
                                    <span className="image fit"><img
                                        src={bannerpic2}
                                        data-clip="hexagon"
                                        alt="destaque peq banner"
                                    /></span>
                                </div>
                            </div>
                        </div>
                        <p>
                            <Link className="button primary icon fa-eye to-front mobile-only" to="/test">
                                SAIBA MAIS
                            </Link>
                        </p>
                    </div>
                    <div className="col-1 col-12-medium"></div>
                </div>
            </Carousel>
        </>
    );
}